import React from 'react';
import { Routes, BrowserRouter as Router, Route ,HashRouter} from 'react-router-dom'
import Footer from './components/Footer';
import Topnav from './components/Topnav';
import Blogs from './pages/Blogs';
import Contact from './pages/Contact';
import Docs from './pages/Docs';
import Home from './pages/Home';
import ImageConvert from './pages/ImageConvert';
import PostDetails from './pages/PostDetails';
import Privacy from './pages/Privacy';
import Terms from './pages/Terms';

const App = () => {
  return (
    <div>
      <HashRouter>
        <Topnav />
        <Routes>
          <Route path="/" element={<Home />} exact />
          <Route path="/blogs" element={<Blogs />} exact />
          <Route path="/contact" element={<Contact />} exact />
          <Route path="/blog/:blog_slug" element={<PostDetails />} exact />
          <Route path="/tnpsc-photo-sign-converter" element={<ImageConvert />} exact />
          <Route path="/privacy-policy" element={<Privacy />} exact />
          <Route path="/terms-and-conditions" element={<Terms />} exact />
          {/* <Route path="/docs" element={<Docs />} exact /> */}
        </Routes>
        <Footer />
      </HashRouter>
    </div>
  )
}
export default App
