import React, { useState } from "react";
import { Tab } from '@headlessui/react'
import { darkColor, lightColor, PrimaryColor } from "../Colors";
import Logo from '../../src/images/logo.jpg'

function resizeImage(base64Str, maxWidth = 400, maxHeight = 350) {
    return new Promise(resolve => {
        let img = new Image();
        img.src = base64Str;
        img.onload = () => {
            let canvas = document.createElement("canvas");
            const MAX_WIDTH = maxWidth;
            const MAX_HEIGHT = maxHeight;
            let width = img.width;
            let height = img.height;
            let shouldResize = false;

            if (width > height) {
                if (width > MAX_WIDTH) {
                    height *= MAX_WIDTH / width;
                    width = MAX_WIDTH;
                    shouldResize = true;
                }
            } else {
                if (height > MAX_HEIGHT) {
                    width *= MAX_HEIGHT / height;
                    height = MAX_HEIGHT;
                    shouldResize = true;
                }
            }
            if (shouldResize) {
                canvas.width = width;
                canvas.height = height;
                let ctx = canvas.getContext("2d");
                ctx.drawImage(img, 0, 0, width, height);
                resolve(canvas.toDataURL("image/jpeg", 0.9));
            } else {
                resolve(base64Str);
            }
        };
    });
}


export default function ImageConvert() {
    const [imagePreviewUrl, setImagePreviewUrl] = useState(null);
    const [imagePhotoResizedUrl, setImagePhotoResizedUrl] = useState(null);

    const handlephotoChange = e => {
        const reader = new FileReader();
        reader.onload = function (e) {
            setImagePreviewUrl(e.target.result);

            resizeImage(e.target.result, 138, 177).then(result => {
                setImagePhotoResizedUrl(result);
            });
        };
        reader.readAsDataURL(e.target.files[0]);
    };

    const [imageSignResizedUrl, setImageSignResizedUrl] = useState(null);

    const handlesignChange = e => {
        const reader = new FileReader();
        reader.onload = function (e) {
            setImagePreviewUrl(e.target.result);

            resizeImage(e.target.result, 132.28, 56.7).then(result => {
                setImageSignResizedUrl(result);
            });
        };
        reader.readAsDataURL(e.target.files[0]);
    };

    return (
        <div className={`dark:bg-${darkColor}`}>
            <div className="container px-5 py-12 mx-auto flex">
                <div className="lg:w-1/2 mx-auto flex flex-wrap">
                    {imagePreviewUrl ? <img src={imagePreviewUrl} className="lg:w-1/2 w-full h-64 object-cover object-center rounded" />
                        :
                        <img className="lg:w-1/2 w-full h-64 object-cover object-center rounded dark:border-2 dark:boder-white" src={Logo} />}
                    <div className="lg:w-1/2 w-full lg:pl-10 lg:py-6 mt-6 lg:mt-0">
                        <h1 className="text-gray-900 dark:text-white text-3xl title-font font-medium mb-1">TNPSC Photo and Signature Converter Tool Online</h1>

                        <Tab.Group>
                            <Tab.List>
                                <Tab className={`${PrimaryColor} mr-2 border-2 border-${lightColor} hover:text-${lightColor} hover:bg-transparent dark:border-[#1efd7b] dark:bg-[#1efd7b] dark:hover:bg-transparent text-white dark:text-black dark:hover:text-[#1efd7b] text-sm inline-flex items-center md:mb-2 lg:mb-0 p-2 rounded-lg`}>Photo</Tab>
                                <Tab className={`${PrimaryColor} ml-2 border-2 border-${lightColor} hover:text-${lightColor} hover:bg-transparent dark:border-[#1efd7b] dark:bg-[#1efd7b] dark:hover:bg-transparent text-white dark:text-black dark:hover:text-[#1efd7b] text-sm inline-flex items-center md:mb-2 lg:mb-0 p-2 rounded-lg`}>Signature</Tab>
                            </Tab.List>
                            <Tab.Panels>
                                {/* photo */}
                                <Tab.Panel>
                                    <div className="relative my-4">
                                        <label for="photo-upload" className="leading-7 text-sm text-gray-600 dark:text-gray-200">Photo</label>
                                        <input type="file" id="photo-upload" accept="image/*" onChange={handlephotoChange}
                                            className="w-full rounded border border-gray-300 outline-none text-gray-200 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out" />
                                    </div>
                                    {imagePhotoResizedUrl && <a href={imagePhotoResizedUrl} download className="px-4 py-2 bg-gray-800 text-white">Download</a>}
                                </Tab.Panel>
                                { /* photo */}
                                <Tab.Panel>
                                    <div className="relative my-4">
                                        <label for="sign-upload" className="leading-7 text-sm text-gray-600 dark:text-gray-200">Signature</label>
                                        <input type="file" id="sign-upload" accept="image/*" onChange={handlesignChange}
                                            className="w-full rounded border border-gray-300 outline-none text-gray-200 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out" />
                                    </div>
                                    {imageSignResizedUrl && <a href={imageSignResizedUrl} download className="px-4 py-2 bg-gray-800 text-white">Download</a>}
                                </Tab.Panel>
                            </Tab.Panels>
                        </Tab.Group>
                    </div>
                </div>
            </div>
        </div>
    );
}
